@import 'var';
.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
}

input {
    &::placeholder {
        color: rgba(#22232C, 0.3);
    }
}

.display1 {
    font-family: $orchidea-font;
    font-size: 60px;
    line-height: 120%;
    font-weight: 300;
    @media screen and (max-width: 1200px) {
        font-size: 45px;
    }
    @media screen and (max-width: 992px) {
        font-size: 40px;
    }
}

.display2 {
    font-family: $orchidea-font;
    font-weight: 300;
    font-size: 35px;
    line-height: 120%;
    @media screen and (max-width: 1200px) {
        font-size: 28px;
    }
    @media screen and (max-width: 992px) {
        font-size: 20px;
    }
}

.btn-black {
    background: #33373A;
    color: #FFFFFF;
    &:hover {
        background: #22232C;
        color: #FFFFFF;
    }
}

.btn-link {
    font-weight: 300;
    font-size: 16px;
    line-height: 120%;
    text-decoration-line: underline;
    &:hover {
        color: $accent-color;
    }
}

.product-card {
    position: relative;
    &__hit {
        position: absolute;
        left: 8px;
        top: -10px;
        z-index: 2;
    }
    &:hover {
        .product-card__colors {
            opacity: 1;
        }
    }
    &__top {
        position: relative;
    }
    &__img {
        display: block;
        img {
            display: block;
            width: 100%;
            display: block;
            border-radius: 3px;
        }
    }
    &__favourite {
        position: absolute;
        top: 19px;
        right: 19px;
        width: 26px;
        height: 23px;
        background-image: url(../img/favourite.svg);
        background-size: 100% auto;
        &--active {
            background-image: url(../img/favourite2.svg);
        }
    }
    &__prices {
        margin-top: 16px;
    }
    &__price {
        &-new {
            font-weight: 500;
            font-size: 18px;
            line-height: 120%;
        }
        &-old {
            opacity: 0.7;
            margin-left: 15px;
            text-decoration-line: line-through;
            font-size: 16px;
            line-height: 120%;
        }
    }
    &__title {
        font-size: 14px;
        line-height: 120%;
        display: block;
        margin-top: 1px;
    }
    &__colors {
        margin-top: 5px;
        display: flex;
        align-items: center;
        opacity: 0;
        transition: all .125s;
    }
    &__color {
        border: 1px solid #A3A3A3;
        width: 17.03px;
        height: 17.03px;
        border-radius: 50%;
        display: block;
        &:not(:last-child) {
            margin-right: 7px;
        }
    }
}

.breadcrumbs {
    margin-top: 40px;
    &__item {
        font-size: 16px;
        line-height: 120%;
        &:hover {
            color: $accent-color;
        }
        &:not(:last-child) {
            &::after {
                content: ' /';
            }
        }
        &:last-child {
            opacity: 0.5;
            pointer-events: none;
        }
    }
}

@media screen and (max-width: 992px) {
    .product-card {
        &__colors {
            opacity: 1;
        }
    }
    .breadcrumbs {
        display: none;
    }
}

@media screen and (max-width: 768px) {
    .product-card {
        background: #FFFFFF;
        border-radius: 3px;
        padding: 20px 25px;
        &__prices {
            margin-top: 5px;
        }
        &__price {
            &-new {
                font-size: 14px;
            }
            &-old {
                font-size: 13px;
            }
        }
        &__title {
            font-size: 13px;
        }
    }
}